<script>
export default {
name: 'AboutView',
    data: () => ({
      dialog_err:false,
      dialog: false,
      firstName:"",
      lastName:"",
      userName:"",
      email:"",
      loginopen:true,
      logupopen:false,
      check:"",
      password:"",
      passwordgood:"",

    }),
    mounted (){
        var coint = 0;
        if(localStorage.getItem("step") != null){
            coint = localStorage.getItem("step");
        }
        var total;
        if (navigator.geolocation) {
             navigator.geolocation.watchPosition(function (position) {
                    var location;  
                if(location != position.timestamp){
                    location = position.timestamp;
                    coint ++;
                }
                var money;
                
        money = 0.01;
        document.getElementById("name").innerHTML = localStorage.getItem("firstname")+ " " + localStorage.getItem("lastname");
        setInterval(() => {
            total = parseFloat(money) + parseFloat(coint/100);
            document.getElementById("money").innerHTML = total + " $" ;
            document.getElementById("step").innerHTML = "Number of steps: " + coint;           
            document.getElementById("lbp").innerHTML = "Money of lebanon: "+"<br>" + parseFloat(total * 29.5)+ " LBP" ;
            localStorage.setItem("step" , coint);
        }, 1000);
            },
            function(error){
                console.log(error);
                alert("sorry sir this site is HTTP protocol so geolocation is not working");
                document.getElementById("money").innerHTML = 0 + " $" ;
            document.getElementById("step").innerHTML = "Number of steps: " + coint;           
            document.getElementById("lbp").innerHTML = "Money of lebanon: "+"<br>" + 0 + " LBP" ;
            })
        }
        // var money;
        // money = 0.01;
        document.getElementById("name").innerHTML += localStorage.getItem("firstname")+ " " + localStorage.getItem("lastname");
        // var total;
        // setInterval(() => {
        //     total = parseFloat(money) + parseFloat(coint/100);
        //     document.getElementById("money").innerHTML = total + " $" ;
        //     document.getElementById("step").innerHTML = "Number of steps: " + coint;           
        //     document.getElementById("lbp").innerHTML = "Money of lebanon: "+"<br>" + parseFloat(total * 29.5)+ " LBP" ;
        //     localStorage.setItem("step" , coint);
        // }, 1000);        
    },
    methods: {
        changename(){
            if(this.firstName == "" || this.lastName == ""){
                alert("enter your name");
            }else{
            localStorage.setItem("firstname",this.firstName);
            localStorage.setItem("lastname",this.lastName);
            this.dialog_err=true; 
            }
        },
        changepassword(){
            if(this.password == "" || this.passwordgood == ""){
                alert("enter your password");
            }else if(this.password != this.passwordgood){
                alert("your passwod is not matched")
            }else{
            localStorage.setItem("firstname",this.firstName);
            localStorage.setItem("lastname",this.lastName);
            this.dialog_err=true; 
            }
        },
        closeDialog(){
        this.dialog_err=false;
      },
     },
}
</script>
